
.s1{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-items: center;
}
.s1>div{
  display: flex;
  justify-content: center;
  align-items: center;
}
.s1>div>p{
  margin-right: 30px;
  background-color: black;
  color: white;
  font-size: 25px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.s2{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.s2>div{
  display: flex;
  justify-content: center;
  align-items: center;
}
.s2>div>p{
  margin-right: 30px;
  background-color: black;
  color: white;
  font-size: 25px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
#main{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 50px auto;
  width: 500px;
}
input{
  width: 20px;
  height: 20px;
}